import React from 'react'
import { Input } from '@vkontakte/vkui'
import Div from '@vkontakte/vkui/dist/components/Div/Div'
import List from '@vkontakte/vkui/dist/components/List/List'
import ComItem from './ComItem'

class Text extends ComItem {
    constructor(props) {
        super(props)
    }

    fields() {
        return (
            <List>
                <Div>
                    <Input type="text" placeholder="Текст команды" name="text" value={this.state.item.text} onChange={this.changeField} />
                </Div>
            </List>
        )
    }
}

export default Text
