class BotApi {
    static queryGET(url) {
        var xhr = new XMLHttpRequest()
        xhr.open('GET', `https://pavlentygood.fvds.ru/query.php?method=GET&url=${encodeURIComponent(url)}`, false)
        xhr.send()
        return xhr.responseText
    }

    static queryPOST(url, body) {
        var xhr = new XMLHttpRequest()
        xhr.open('POST', `https://pavlentygood.fvds.ru/query.php?method=POST&url=${encodeURIComponent(url)}`, false)
        xhr.setRequestHeader('Content-Type', 'application/json')
        xhr.send(JSON.stringify(body))
        return xhr.responseText
    }

    static queryPUT(url, body) {
        var xhr = new XMLHttpRequest()
        xhr.open('POST', `https://pavlentygood.fvds.ru/query.php?method=PUT&url=${encodeURIComponent(url)}`, false)
        xhr.setRequestHeader('Content-Type', 'application/json')
        xhr.send(JSON.stringify(body))
        return xhr.responseText
    }

    static queryDELETE(url) {
        var xhr = new XMLHttpRequest()
        xhr.open('GET', `https://pavlentygood.fvds.ru/query.php?method=DELETE&url=${encodeURIComponent(url)}`, false)
        xhr.setRequestHeader('Content-Type', 'application/json')
        xhr.send()
        return xhr.responseText
    }

    static getUser(id) {
        return JSON.parse(this.queryGET(`app_info?userId=${id}`))
    }

    static getAllCommands() {
        return JSON.parse(this.queryGET(`simple_command`))
    }

    static addCommand(body) {
        this.queryPOST('simple_command', body)
    }

    static updateCommand(body) {
        this.queryPUT('simple_command', body)
    }

    static deleteCommand(id) {
        this.queryDELETE(`simple_command?id=${id}`)
    }
}

export default BotApi
