import React from 'react'
import bridge from '@vkontakte/vk-bridge'
import View from '@vkontakte/vkui/dist/components/View/View'
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner'
import '@vkontakte/vkui/dist/vkui.css'
import Home from './panels/Home'
import ComList from './panels/com_constructor/ComList'
import ComConstructor from './panels/com_constructor/ComConstructor'
import ComEditor from './panels/com_constructor/ComEditor'

class App extends React.Component {
	constructor() {
		super()
		this.state = {
			fetchedUser: null,
			activePanel: 'home',
			popout: <ScreenSpinner size='large' />
		}
		this.getParam = this.getParam.bind(this)
		this.go = this.go.bind(this)
		this.setPopout = this.setPopout.bind(this)

		async function fetchData(obj) {
			const user = await bridge.send('VKWebAppGetUserInfo')
			obj.setState(ps => {
			  ps.fetchedUser = user
			  ps.popout = null
			  return ps
			})
		  }
		  fetchData(this)
	}

	getParam(key) {
		var p = window.location.search
		p = p.match(new RegExp(key + '=([^&=]+)'))
		return p ? p[1] : false
	}

	go(e) {
		const panel = e.currentTarget.dataset.to
		this.setState(ps => {
			ps.activePanel = panel
			return ps
		})
	}

	setPopout(popout) {
		this.setState(ps => {
			ps.popout = popout
			return ps
		})
	}

	render() {
		const userId = this.getParam('user_id')
		return (
			<View activePanel={this.state.activePanel} popout={this.state.popout}>
				<Home id='home' user={this.state.fetchedUser} go={this.go} userId={userId} app={this} />
				<ComList id='com_list' go={this.go} userId={userId} app={this} />
				<ComConstructor id='com_constructor' go={this.go} userId={userId} app={this} />
				<ComEditor id='com_editor' go={this.go} userId={userId} app={this} />
			</View>
		)
	}
}

export default App
