import React from 'react'
import { platform, IOS } from '@vkontakte/vkui'
import Alert from '@vkontakte/vkui/dist/components/Alert/Alert'
import BotApi from '../../BotApi'
import ComPanel from './ComPanel'

class ComEditor extends ComPanel {
    constructor(props) {
        super(props)
        this.header = 'Просмотр команды'
        this.actionText = 'Сохранить'

        const com = props.app.command
        this.state.comName = com.Name
        this.state.id = com.Id
        if (com.Text != null) {
            this.state.text.status = true
            this.state.text.text = com.Text
        }
        if (com.Post != null) {
            this.state.post.status = true
            this.state.post.type = com.Post.Type
            this.state.post.owner = com.Post.OwnerId + ''
            this.state.post.id = com.Post.PostId + ''
            this.state.post.number = com.Post.Number + ''
        }
        if (com.Image != null) {
            this.state.image.status = true
            this.state.image.type = com.Image.Type
            this.state.image.owner = com.Image.OwnerId + ''
            this.state.image.ids = com.Image.Ids.split(' ').map(a => a + '')
            this.state.image.album = com.Image.AlbumId + ''
        }
        if (com.Video != null) {
            this.state.video.status = true
            this.state.video.type = com.Video.Type
            this.state.video.owner = com.Video.OwnerId + ''
            this.state.video.ids = com.Video.Ids.split(' ').map(a => a + '')
            this.state.video.album = com.Video.AlbumId + ''
        }
    }

    updateCommand(body) {
        BotApi.updateCommand(body)

        this.props.app.go({currentTarget: { dataset: { to: 'com_list'}}})
        this.props.app.setPopout(
            <Alert
                actions={[{
                    title: 'ОК',
                    mode: 'cancel',
                    autoclose: true,
                }]}
                onClose={() => this.props.app.setPopout(null)}
            >
                <p>Изменения сохранены!</p>
            </Alert>
        )
    }

    action(body) {
        this.updateCommand(body)
    }
}

export default ComEditor
