import React from 'react'
import { Input, Select } from '@vkontakte/vkui'
import Div from '@vkontakte/vkui/dist/components/Div/Div'
import Button from '@vkontakte/vkui/dist/components/Button/Button'
import List from '@vkontakte/vkui/dist/components/List/List'
import ComItem from './ComItem'

class Media extends ComItem {
    constructor(props) {
        super(props)
        this.getIdsMap = this.getIdsMap.bind(this)
        this.addId = this.addId.bind(this)
        this.deleteId = this.deleteId.bind(this)
        this.changeId = this.changeId.bind(this)
        this.checkIdsCount = this.checkIdsCount.bind(this)
    }

    selectItemType(e) {
        super.selectItemType(e)
        if (e.target.value == 'id' && this.state.item.ids.length == 0) {
            this.addId()
        } else if (this.state.item.ids.length > 0) {
            this.deleteId()
        }
    }

    changeId(e) {
        const n = e.currentTarget.name
        const v = e.currentTarget.value
        this.props.cc.setState(ps => {
            if (v == '' || !isNaN(v)) {
                this.state.item.ids[n] = v
            }
            return ps
        })
    }

    getIdsMap() {
        return this.state.item.ids.map((id, i) => (
            <Div hidden={this.setFieldHidden('id')}>
                <Input
                    name={i}
                    value={id}
                    type="text"
                    placeholder="ID медиавложения"
                    value={this.state.item.ids[i]}
                    onChange={this.changeId}
                />
            </Div>
        ))
    }

    addId(e) {
        this.props.cc.setState(ps => {
            this.state.item.ids.push('')
            return ps
        })
    }

    deleteId(e) {
        this.props.cc.setState(ps => {
            this.state.item.ids.pop()
            return ps
        })
    }

    checkIdsCount() {
        var count = 0
        this.props.cc.idsItems.forEach(item => count += item.ids.length)
        return count < 10
    }

    fields() {
        return (
            <List>
                <Div>
                    <Select placeholder="Выберите тип" value={this.state.item.type} onChange={this.selectItemType}>
                        <option value="id">По ID</option>
                        <option value="random">Случайное</option>
                    </Select>
                </Div>
                <Div hidden={this.state.item.type == null}>
                    <Input type="text" placeholder="ID владельца" name="owner" value={this.state.item.owner} onChange={this.changeField} />
                </Div>
                <Div hidden={this.setFieldHidden('random')}>
                    <Input type="text" placeholder="ID альбома" name="album" value={this.state.item.album} onChange={this.changeField} />
                </Div>
                {this.getIdsMap()}
                <Div hidden={this.setFieldHidden('id')}>
                    <Button size="s" disabled={!this.checkIdsCount()} onClick={this.addId}>Добавить ID</Button>
                    <Button size="s" mode="destructive" disabled={this.state.item.ids.length <= 1} onClick={this.deleteId}>Удалить ID</Button>
                </Div>
            </List>
        )
    }
}

export default Media
