import React from 'react'
import { platform, IOS } from '@vkontakte/vkui'
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel'
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader'
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton'
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back'
import Icon24Back from '@vkontakte/icons/dist/24/back'
import Div from '@vkontakte/vkui/dist/components/Div/Div'
import Button from '@vkontakte/vkui/dist/components/Button/Button'
import Group from '@vkontakte/vkui/dist/components/Group/Group'
import List from '@vkontakte/vkui/dist/components/List/List'
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell'
import BotApi from '../../BotApi'

const osName = platform()

class ComList extends React.Component {
    constructor() {
        super()
        this.openCommand = this.openCommand.bind(this)
    }

    openCommand(e) {
        const i = e.currentTarget.dataset.comindex
        this.props.app.setState(ps => {
            ps.activePanel = 'com_editor'
            this.props.app.command = this.props.app.comArr[i]
            return ps
        })
    }

    render() {
        this.props.app.comArr = BotApi.getAllCommands()
        return (
            <Panel id={this.props.id}>
                <PanelHeader
                    left={<PanelHeaderButton onClick={this.props.go} data-to="home">
                        {osName === IOS ? <Icon28ChevronBack /> : <Icon24Back />}
                    </PanelHeaderButton>}
                >Конструктор команд</PanelHeader>
                <Group>
                    <Div>
                        <Button size="l" onClick={this.props.go} data-to="com_constructor">Создать команду</Button>
                    </Div>
                    <Div>
                        <List>
                            {this.props.app.comArr.map((a, i) => (<Cell onClick={this.openCommand} data-comindex={i}>{a.Name}</Cell>))}
                        </List>
                    </Div>
                </Group>
            </Panel>
        )
    }
}

export default ComList
