import React from 'react'
import { platform, IOS } from '@vkontakte/vkui'
import Alert from '@vkontakte/vkui/dist/components/Alert/Alert'
import BotApi from '../../BotApi'
import ComPanel from './ComPanel'

const osName = platform()

class ComConstructor extends ComPanel {
    constructor(props) {
        super(props)
        this.header = 'Создание команды'
        this.actionText = 'Создать команду'
    }

    createCommand(body) {
        BotApi.addCommand(body)

        this.props.app.go({currentTarget: { dataset: { to: 'com_list'}}})
        this.props.app.setPopout(
            <Alert
                actions={[{
                    title: 'ОК',
                    mode: 'cancel',
                    autoclose: true,
                }]}
                onClose={() => this.props.app.setPopout(null)}
            >
                <p>Команда создана!</p>
            </Alert>
        )
    }

    action(body) {
        this.createCommand(body)
    }
}

export default ComConstructor
