import React from 'react'
import Group from '@vkontakte/vkui/dist/components/Group/Group'
import Header from '@vkontakte/vkui/dist/components/Header/Header'
import Button from '@vkontakte/vkui/dist/components/Button/Button'

class ComItem extends React.Component {
    constructor(props) {
        super()
        this.state = {
            item: props.item
        }
        this.selectItemType = this.selectItemType.bind(this)
        this.setFieldHidden = this.setFieldHidden.bind(this)
        this.changeField = this.changeField.bind(this)
        this.deleteItem = this.deleteItem.bind(this)
    }

    selectItemType(e) {
        const v = e.target.value
        this.props.cc.setState(ps => {
            this.state.item.type = v.length == 0 ? null : v
            return ps
        })
    }

    setFieldHidden(type) {
        return !(this.state.item.type == type)
    }

    changeField(e) {
        const n = e.currentTarget.name
        const v = e.currentTarget.value
        this.props.cc.setState(ps => {
            if (v == '' || n == 'text' || !isNaN(v)) {
                this.state.item[n] = v
            }
            return ps
        })
    }

    deleteItem() {
        this.props.cc.setState(ps => {
            this.state.item.status = false
            return ps
        })
    }

    render() {
        return (
            <Group
                hidden={!this.state.item.status}
                header={<Header mode="secondary">{this.state.item.rusName}
                    <Button size="s" mode="destructive" onClick={this.deleteItem}>Удалить</Button>
                </Header>}>
                {this.fields()}
            </Group>
        )
    }
}

export default ComItem
